        //  get number functions
        function gn_getItemIndex(num, dev, org, items, $cfs) {
            if (is_string(num))
            {
                num = $(num, $cfs);
            }

            if (is_object(num))
            {
                num = $(num, $cfs);
            }
            if (is_jquery(num))
            {
                num = $cfs.children().index(num);
                if (!is_boolean(org))
                {
                    org = false;
                }
            }
            else
            {
                if (!is_boolean(org))
                {
                    org = true;
                }
            }
            if (!is_number(num))
            {
                num = 0;
            }
            if (!is_number(dev))
            {
                dev = 0;
            }

            if (org)
            {
                num += items.first;
            }
            num += dev;
            if (items.total > 0)
            {
                while (num >= items.total)
                {
                    num -= items.total;
                }
                while (num < 0)
                {
                    num += items.total;
                }
            }
            return num;
        }

